
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components: {
XHeader, Flexbox, Cell, Group,
},
data() {
return {
routeName: this.$route.name,
personalName: '再睡一夏',
personalSex: '男',
personalPhone: '13713749000',
personalPosition: '校长大人',
personalCardNumber: '123456789',
personalSchool: '宝安高级中学',
personalClass: '三年级四班',
url: '../../../../static/imgs/my.jpg',
}

},
methods: {
goChangePhone() {
this.$router.push({ path: '/my/changePhone', query: { phone: this.personalPhone } });
},
goChangeImg() {
this.$router.push({ path: '/my/changeImg', query: { imgUrl: this.url } });
}
},
created() {
document.title = this.$route.name
}
}



